<template>
<div id="explore-main-container">
    <!--<div v-if="!$store.state.userLoggedIn" class="refresh-button-box">
        <vs-button class="refresh-button" block @click="fetchAccountData()">Refresh <i class="fas fa-undo"></i></vs-button>
    </div>-->
    <section id="header-section">
        <!--Explore the community activity headers. and navigation -->
        <h3 class="explore-main-header">Explore</h3>
        <p class="explore-follow-text">See what mixam business community has been up to.</p>
    </section>
    <section id="explore-tab-options-section">
        <div class="explore-tab-container">
            <div @click="setActiveTab('all')" class="item" :class="{'active-tab' : allActive}">
                <p>All</p>
            </div>
            <div @click="setActiveTab('posts')" class="item" :class="{'active-tab' : postActive}">
                <p>Posts</p>
            </div>
            <div @click="setActiveTab('articles')" class="item" :class="{'active-tab' : articlesActive}">
                <p>Articles</p>
            </div>
            <div @click="setActiveTab('courses')" class="item" :class="{'active-tab' : courseActive}">
                <p>Courses</p>
            </div>
        </div>
    </section>
    <section class="explore-feed-container">
        <!--Activity Feed container here. -->
        <keep-alive>
            <component :is="currentFeed"></component>
        </keep-alive>
        <div>
            <feed v-if="false"></feed>
            <posts v-if="false"></posts>
            <articles v-if="false"></articles>
            <courses v-if="false"></courses>            
        </div>
    </section>
</div>
</template>
<script>
import Feed from "./explore-feed.vue";
import Posts from "./explore-post.vue";
import Articles from "./explore-articles.vue";
import Courses from "./explore-courses.vue";

export default {
    data : function (){
        return {
            currentFeed : Feed,
            activeTab : "all"
        }
    },
    created : function() {
        this.$store.commit("updatecurrentroute", "explore");
    },
    computed : {
        allActive : function () {
            return this.activeTab == "all";
        },
        postActive : function () {
            return this.activeTab == "posts";
        },
        articlesActive : function (){
            return this.activeTab == "articles"
        },
        courseActive : function () {
            return this.activeTab == "courses";
        }
    },
    methods  : { 
      setActiveTab (tab) {
          this.activeTab = tab;
          if(tab == "all"){
              this.currentFeed = Feed
          }
          else if(tab == "posts"){
              this.currentFeed = Posts;
          }
          else if(tab == "articles"){
              this.currentFeed = Articles
          }
          else {
              this.currentFeed = Courses
          }
      }  
    },
    components : {
        Feed,Posts,Articles,Courses
    }
}
</script>

<style lang="scss">
    .refresh-button-box{
        width: 90%;
        margin: 0px auto;
        display: block;
        margin-top: 25%;
    }
    .refresh-button,.block-button{
        border-radius: 5px;
        width: 100%;
        margin: 0px auto;
        display: block;
    }

    .explore-main-header{
        padding-left : 8px;
        padding-top: 8px;
        margin-bottom: 0px;
    font-weight: 900;
    color: #4e4e4e;
    }
    .explore-follow-text{
            color: #8a8a8a;
    font-size: 90%;
    padding-left: 8px;
    }
    .item.active-tab{
        background: #00BCD4;
    font-weight: 900;
    color: white;
    }
    .explore-tab-container{
        display: flex;
        padding-top: 8px;
        padding-left : 8px;
    }
    .explore-tab-container div{
        width: max-content;
    }
    .explore-tab-container .item {
        margin: 4px 8px 0px 0px;
    border-radius: 10px;
    padding: 0px 16px;
    box-shadow: 0px 0px 3px grey;
    }
    .item:hover{
        cursor: pointer;
    }
    #explore-main-container{
            padding-bottom: 70px;
            padding-left : 8px;
            padding-right: 8px;
    }
</style>
